  border: 1px solid #C84B31 !important;
  font-size: 1.8rem;
  display: grid;
  min-height: 125px;
  padding: .25rem;
  border: 1px solid #C84B31 !important;
}
.card-episode{
    position: absolute;
    top: -45px;
    left: 55%;
    width: 65px;
    height: 65px;
    line-height: 65px;
    border-radius: 50%;
    font-size: 30px;
    color: #ECDBBA;
    text-align: center;
    background: #C84B31
  }
.card-season{
    position: absolute;
    top: -25px;
    left: 75%;
    width: 50px;
    height: 50px;
    line-height: 50px;
    border-radius: 50%;
    font-size: 30px;
    color: #ECDBBA;
    text-align: center;
    background: #C84B31
  }
.card-subtitle{
    padding-top: 15%;
  }
.shrink {
    transition: 1s ease;
    }
.shrink:hover{
    -webkit-transform: scale(0.8);
    -ms-transform: scale(0.8);
    transform: scale(0.8);
    transition: 1s ease;
    }